.container {
  width: 80%;
  height: 100vh;
  margin: 0px auto;
  margin-top: -0px;
  position: relative;
}
.mySlides {
  display: none;
}

.cursor {
  cursor: pointer;
}

.coffe-text {
  font-size: 25px;
  position: absolute;
  padding: 60px;
  color: rgb(247, 238, 238);
  background: black;
  margin-top: -4%;
  margin-left: 80px;
}
.prev,
.next {
  cursor: pointer;
  position: relative;
  top: 80%;
  width: auto;
  padding: 16px;
  margin-top: -6px;
  margin-left: 80%;
  color: rgb(13, 12, 12);
  font-weight: lighter;
  font-size: 40px;
}
.prev {
  margin-right: 20px;
}
.next {
  margin-left: 20px;
  border-radius: 3px 0 0 3px;
}
.navgation {
  display: flex;
  margin-top: 3%;
  margin-right: -1%;
}
