nav {
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: black;
  padding: 20px 80px;
  box-shadow: 0.5px 15px rgba(0, 0, 0, 0.06);
}
#navbar {
  display: flex;
  align-items: center;
  justify-content: center;
}
#navbar li {
  list-style: none;
  padding: 0 20px;
  position: relative;
}
#navbar li a {
  text-decoration: none;
  font-size: 1.3rem;
  font-weight: 600;
  color: white;
  transition: 0.3s ease-in-out;
}
#navbar li a:hover,
#navbar li a.active {
  color: red;
}

#navbar li a:hover::after,
#navbar li a:active::after {
  content: "";
  width: 30%;
  height: 2px;
  background: red;
  position: absolute;
  bottom: -4px;
  left: 20px;
}
#mobile {
  display: none;
}
#mobile i {
  color: white;
  align-items: center;
}
.navbar-search {
  display: inline;
}
.dib {
  display: inline-flex;
}
.ph3 {
  padding-left: 1rem;
  padding-right: 1rem;
}
.text-center {
  text-align: center;
}
.search-input {
  display: flex;
}
.db {
  display: none;
}

@media screen and (max-width: 375px) {
  .logo {
    width: 300px;
    height: auto;
  }
  #navbar {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    position: fixed;
    top: 90px;
    right: -300px;
    width: 300px;
    height: 100vh;
    background: black;
    box-shadow: 0 40px 60px rgba(0, 0, 0, 0.1);
    padding: 40px 0 0 10px;
    transition: 0.3s ease-in-out;
  }
  #navbar.active {
    right: 0;
  }
  #navbar li {
    margin-bottom: 25px;
  }
  #mobile {
    display: block;
  }
  #mobile i {
    font-size: 22px;
    cursor: pointer;
  }
  .dib {
    display: flex;
  }
  .navbar-search {
    font-size: 25px;
  }
  .search-input {
    display: flex;
  }
  .social-icons {
    display: none;
  }
  .text-center {
    display: none;
  }
  .fl {
    display: none;
  }
  .db {
    display: inline;
  }
}

@media screen and (max-width: 769px) {
  .logo {
    width: 500px;
    height: auto;
  }

  #navbar {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    position: fixed;
    top: 90px;
    right: -300px;
    width: 300px;
    height: 100vh;
    background: black;
    box-shadow: 0 40px 60px rgba(0, 0, 0, 0.1);
    padding: 40px 0 0 10px;
    transition: 0.3s ease-in-out;
  }

  #navbar.active {
    right: 0;
  }
  #navbar li {
    margin-bottom: 25px;
  }
  .navbar-search {
    width: 50%;
    font-size: 25px;
  }
  #mobile {
    display: block;
  }
  #mobile i {
    font-size: 24px;
    cursor: pointer;
  }
  .social-icons {
    display: none !important;
  }
  .text-center {
    display: none;
  }
  .fl {
    display: none;
  }
  .db {
    display: inline;
    margin-top: 500px;
    bottom: 0;
  }
}

@media screen and (max-width: 1200px) {
  #navbar {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    position: fixed;
    top: 90px;
    right: -300px;
    width: 300px;
    height: 100vh;
    background: black;
    box-shadow: 0 40px 60px rgba(0, 0, 0, 0.1);
    padding: 40px 0 0 10px;
    transition: 0.3s ease-in-out;
  }

  #navbar.active {
    right: 0;
  }
  #navbar li {
    margin-bottom: 25px;
  }
  #mobile {
    display: block;
    margin-left: 30px;
  }
  #mobile i {
    font-size: 35px;
    cursor: pointer;
  }
  .navbar-search {
    font-size: 30px;
    display: inline;
    margin-left: -100px;
  }
  .social-icons {
    display: none !important;
  }
  .text-center {
    display: none;
  }
  .fl {
    display: none;
  }
  .db {
    display: inline;
    margin-top: 300px;
    bottom: 0;
  }
}
